import React, { Component } from 'react';
import WowItemList from 'components/wowitem/WowItemList';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as wowitemActions from 'store/modules/wowitem';

class WowItemListContainer extends Component {

  getWowItemList = async() => {
    const {WowitemActions} = this.props;
    try {
      await WowitemActions.getWowItemList();
    } catch(e) {
      console.log(e);
    }
  }

  componentDidMount() {
    const {wowitems} = this.props;
    if(!wowitems.length > 0) this.getWowItemList();
  }

  selStat = (stat) => {
    const {WowitemActions} = this.props;
    WowitemActions.setStatOption(stat);
  }

  selStat1 = (stat) => {
    const {WowitemActions} = this.props;
    WowitemActions.setStatOption1(stat);
  }

  selSubClass = (subClass) => {
    const {WowitemActions} = this.props;
    WowitemActions.setItemSubClass(subClass);
  }

  setInvenType = (invenType) => {
    const {WowitemActions} = this.props;
    WowitemActions.setInvenType(invenType);
  }

  setSelOneStat = (selOneStat) => {
    const {WowitemActions} = this.props;
    WowitemActions.setSelOneStat(selOneStat);
  }

  render() {
    const {wowitems, isCri, isHas, isMas, isVer, itemSubClass, nonStat, selInvenType, selOneStat} = this.props;
    const {selStat, selSubClass, selStat1, setInvenType, setSelOneStat} = this;

    return (
      
      <WowItemList
      wowitems={wowitems}
      selStat={selStat}
      selStat1={selStat1}
      selSubClass={selSubClass}
      setInvenType={setInvenType}
      setSelOneStat={setSelOneStat}
      selInvenType={selInvenType}
      isCri={isCri}
      isHas={isHas}
      isMas={isMas}
      isVer={isVer}
      nonStat={nonStat}
      selOneStat={selOneStat}
      itemSubClass={itemSubClass}
    //   isStockDownView={isStockDownView}
    //   todayMax={todayMax}
      />
    );
  }
}

export default connect(
  (state) => ({
    wowitems: state.wowitem.toJS().wowitems,
    isCri: state.wowitem.toJS().isCri,
    isHas: state.wowitem.toJS().isHas,
    isMas: state.wowitem.toJS().isMas,
    isVer: state.wowitem.toJS().isVer,
    nonStat: state.wowitem.toJS().nonStat,
    selOneStat: state.wowitem.toJS().selOneStat,
    itemSubClass: state.wowitem.toJS().itemSubClass,
    selInvenType: state.wowitem.toJS().selInvenType
  }),
  (dispatch) => ({
    WowitemActions: bindActionCreators(wowitemActions, dispatch)
  })
)(WowItemListContainer);