import {createAction, handleActions} from 'redux-actions';
import {Map, List, fromJS} from 'immutable';
import {pender} from 'redux-pender';
import * as api from 'lib/api';

const GET_ITEM_LIST = 'wowitem/GET_ITEM_LIST';
const SET_STAT_OPTION = 'wowitem/SET_STAT_OPTION';
const SET_STAT_OPTION1 = 'wowitem/SET_STAT_OPTION1'; //non stat set
const SET_ITEM_SUBCLASS = 'wowitem/SET_ITEM_SUBCLASS';
const SET_INVEN_TYPE = 'wowitem/SET_INVEN_TYPE';
const SET_SELONESTAT = 'wowitem/SET_SELONESTAT';

export const getWowItemList = createAction(GET_ITEM_LIST, api.getWowItemList);
export const setStatOption = createAction(SET_STAT_OPTION);
export const setStatOption1 = createAction(SET_STAT_OPTION1);
export const setItemSubClass = createAction(SET_ITEM_SUBCLASS);
export const setInvenType = createAction(SET_INVEN_TYPE);
export const setSelOneStat = createAction(SET_SELONESTAT);

const initialState = Map({
  wowitems: List(),
  isCri: "N",
  isHas: "N",
  isMas: "N",
  isVer: "N",
  nonStat: "",
  selOneStat: "",
  selInvenType: "",
  itemSubClass: ""
});

export default handleActions({
  ...pender({
    type: GET_ITEM_LIST,
    onSuccess: (state, action) => {
      const {data: items} = action.payload;
      return state.set('wowitems', fromJS(items))
                  ;
    }
  }),
  [SET_STAT_OPTION]: (state, action) => {
    const value = action.payload;
    const curIsCri = state.toJS().isCri;
    const curIsHas = state.toJS().isHas;
    const curIsMas = state.toJS().isMas;
    const curIsVer = state.toJS().isVer;

    let selCnt = 0;

    if(curIsCri == "Y") selCnt += 1;
    if(curIsHas == "Y") selCnt += 1;
    if(curIsMas == "Y") selCnt += 1;
    if(curIsVer == "Y") selCnt += 1;

    if(value == "치명") {
        if(curIsCri == "Y") {
          return state.set('isCri', "N");
        }else{
          if(selCnt > 1) return state;
          else return state.set('isCri', "Y");
        }
    }else if(value == "가속") {
      if(curIsHas == "Y") {
        return state.set('isHas', "N");
      }else{
        if(selCnt > 1) return state;
          else return state.set('isHas', "Y");
      }
    }else if(value == "특화") {
      if(curIsMas == "Y") {
        return state.set('isMas', "N");
      }else{
        if(selCnt > 1) return state;
          else return state.set('isMas', "Y");
      }
    }else if(value == "유연") {
      if(curIsVer == "Y") {
        return state.set('isVer', "N");
      }else{
        if(selCnt > 1) return state;
          else return state.set('isVer', "Y");
      }
    }
  },
  [SET_ITEM_SUBCLASS]: (state, action) => {
    const value = action.payload;
    const itemSubClass = state.toJS().itemSubClass;
    if(value == itemSubClass) {
      return state.set('itemSubClass', '');
    }else{
      return state.set('itemSubClass', value);
    }    
  },
  [SET_STAT_OPTION1]: (state, action) => {
    const value = action.payload;
    console.log(value);
    const nonStat = state.toJS().nonStat;
    if(value == nonStat) {
      return state.set('nonStat', '');
    }else{
      return state.set('nonStat', value);
    }    
  },
  [SET_INVEN_TYPE]: (state, action) => {
    const value = action.payload;
    const selInvenType = state.toJS().selInvenType;
    if(value == selInvenType) {
      return state.set('selInvenType', '');
    }else{
      return state.set('selInvenType', value);
    }    
  },
  [SET_SELONESTAT]: (state, action) => {
    const value = action.payload;
    const selOneStat = state.toJS().selOneStat;
    if(value == selOneStat) {
      return state.set('selOneStat', '');
    }else{
      return state.set('selOneStat', value);
    }    
  }
}, initialState);