import React from 'react';
import styles from './WowItemList.scss';
import Adsense970250 from 'components/adsense/Adsense970250';
import classNames from 'classnames/bind';
// import NumberFormat from 'react-number-format';

const cx = classNames.bind(styles);

const WowItemList = ({wowitems, selStat, isCri, isHas, isMas, isVer, itemSubClass, selSubClass, 
  selStat1, nonStat, setInvenType, selInvenType, setSelOneStat, selOneStat}) => {
  
  let selStatCnt = 0;
  if(isCri == "Y") selStatCnt += 1;
  if(isHas == "Y") selStatCnt += 1;
  if(isMas == "Y") selStatCnt += 1;
  if(isVer == "Y") selStatCnt += 1;

  const subclassAll = ["판금", "사슬", "가죽", "천"];
  const subclassFilter = subclassAll.filter((a) => a != itemSubClass);

  const list = wowitems.map((wowitem, index_num) => {
    // if(index_num >= 11 && index_num <= 30) {
      const {
        zone, voss, voss_seq, info,
        _id
      } = wowitem;
      // console.log(JSON.parse(info));
      let statSameCnt = 0;      
      let statSameNonCnt = 0;      
      let statSameOneStatCnt = 0;      
      const item_id = JSON.parse(info).id;
      const inventory_type = JSON.parse(info).inventory_type;
      const item_subclass = JSON.parse(info).item_subclass;
      const item_name = JSON.parse(info).name;
      
      let stat_value = "";
      let spellDec = "";
      let url = "https://ko.wowhead.com/item="+item_id;      
      
      if(inventory_type.type != 'NON_EQUIP') { //도관제외 착용장비만
        const previewItem = JSON.parse(info).preview_item;

        if(itemSubClass == "" || (item_subclass.name != subclassFilter[0] && item_subclass.name != subclassFilter[1] && item_subclass.name != subclassFilter[2] )) { //특정 재질만이 아니라 특정재질 제외 3개의 재질을 빼는방식
          if(selInvenType == "" || selInvenType == inventory_type.name) {
            if(previewItem.stats != undefined) {
              previewItem.stats.map((stat) => {
                const dispString = stat.display.display_string;

                if(isCri == "Y" && stat.type.type == "CRIT_RATING") {
                  statSameCnt += 1;
                }
                if(isHas == "Y" && stat.type.type == "HASTE_RATING") {
                  statSameCnt += 1;
                }
                if(isMas == "Y" && stat.type.type == "MASTERY_RATING") {
                  statSameCnt += 1;
                }
                if(isVer == "Y" && stat.type.type == "VERSATILITY") {
                  statSameCnt += 1;              
                }

                if(inventory_type.name.indexOf("장비") > -1) {
                  // console.log(item_name, dispString, selOneStat, dispString.indexOf(selOneStat), statSameOneStatCnt);
                  if(selOneStat == "" || dispString.indexOf(selOneStat) > -1) {
                    statSameOneStatCnt += 1;              
                  }
                }else{
                  statSameOneStatCnt += 1;              
                }

                // console.log(dispString);
                if(inventory_type.name.indexOf("장비") > -1 || (dispString.indexOf("힘") == -1 && dispString.indexOf("민첩") == -1 && dispString.indexOf("지능") == -1)) {
                  if(dispString.indexOf("체력") == -1) {
                    stat_value += dispString + " ";
                  }
                }              

                if(inventory_type.name == "장신구") {
                  previewItem.spells.map((spell) => {
                    spellDec += spell.description;
                  });
                }
    
                if(nonStat == stat.type.type) {
                  statSameNonCnt += 1;
                }
              });
            }
            
            if(!statSameNonCnt > 0) {
              if(statSameOneStatCnt >= 1) {
                if( (selStatCnt == 1 && statSameCnt >= 1) || (selStatCnt == 2 && statSameCnt >= 2) ) {
                  const sockets = previewItem.sockets != undefined ? "("+previewItem.sockets[0].socket_type.name+")" : "" ;
                  // console.log(previewItem.sockets != undefined ? previewItem.sockets[0] : "");
                  // console.log(previewItem);
                  return (
                    <>
                      <div className={cx("item-info")}>
                        <a href={url} target='_blank'>
                          [{zone+","+voss}]<br/>
                          {item_name}({previewItem.level.value})<br/>
                          <b>{item_subclass.name} / {inventory_type.name} {sockets}</b><br/>
                          {stat_value}
                          {spellDec == "" ? "" : stat_value == "" ? spellDec : <><br/>{spellDec}</>}
                          <br/><br/>
                        </a>
                      </div>
                      <div className={cx("line")}></div>
                    </>
                  );
                }
              }
            }
          }          
        }
      }   
      
    // }
  });
  
  return (
    <div className={cx('wowitem-list')}>
      <div className={cx('wowitem-adsense')}><Adsense970250/></div>
      <div className={cx('wowitem-search')}>
      <span>방어구재질을 선택하세요.<br/>(반지, 목, 장신구 같은건 무조건 보여집니다.)</span><br/>
        <button className={cx(itemSubClass == '판금' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('판금')}>판금</button>
        <button className={cx(itemSubClass == '사슬' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('사슬')}>사슬</button>
        <button className={cx(itemSubClass == '가죽' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('가죽')}>가죽</button>
        <button className={cx(itemSubClass == '천' ? 'btn-sel' : 'btn-nor')} onClick={() => selSubClass('천')}>천</button>
        <br/><br/>
        <span>찾으려는 2차스탯을 선택하세요. (최소 1개 선택필수)</span><br/>
        <button className={cx(isCri == 'Y' ? 'btn-cri' : 'btn-nor')} onClick={() => selStat('치명')}>치명</button>
        <button className={cx(isHas == 'Y' ? 'btn-has' : 'btn-nor')} onClick={() => selStat('가속')}>가속</button>
        <button className={cx(isMas == 'Y' ? 'btn-mas' : 'btn-nor')} onClick={() => selStat('특화')}>특화</button>
        <button className={cx(isVer == 'Y' ? 'btn-ver' : 'btn-nor')} onClick={() => selStat('유연')}>유연</button>
        <br/><br/>
        <span>제외시킬 2차스탯을 선택하세요.</span><br/>
        <button className={cx(nonStat == 'CRIT_RATING' ? 'btn-cri' : 'btn-nor')} onClick={() => selStat1('CRIT_RATING')}>치명</button>
        <button className={cx(nonStat == 'HASTE_RATING' ? 'btn-has' : 'btn-nor')} onClick={() => selStat1('HASTE_RATING')}>가속</button>
        <button className={cx(nonStat == 'MASTERY_RATING' ? 'btn-mas' : 'btn-nor')} onClick={() => selStat1('MASTERY_RATING')}>특화</button>
        <button className={cx(nonStat == 'VERSATILITY' ? 'btn-ver' : 'btn-nor')} onClick={() => selStat1('VERSATILITY')}>유연</button>
        <br/><br/>
        <span>착용부위를 선택하세요.</span><br/>
        <button className={cx(selInvenType == '한손 장비' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('한손 장비')}>한손장비</button>
        <button className={cx(selInvenType == '보조장비' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('보조장비')}>보조장비</button>
        <button className={cx(selInvenType == '양손 장비' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('양손 장비')}>양손장비</button>
        <button className={cx(selInvenType == '원거리 장비' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('원거리 장비')}>원거리<br/>장비</button>
        <br/>
        <button className={cx(selInvenType == '머리' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('머리')}>머리</button>
        <button className={cx(selInvenType == '어깨' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('어깨')}>어깨</button>
        <button className={cx(selInvenType == '등' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('등')}>등</button>
        <button className={cx(selInvenType == '가슴' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('가슴')}>가슴</button>
        <br/>
        <button className={cx(selInvenType == '손목' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('손목')}>손목</button>
        <button className={cx(selInvenType == '손' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('손')}>손</button>
        <button className={cx(selInvenType == '허리' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('허리')}>허리</button>
        <button className={cx(selInvenType == '다리' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('다리')}>다리</button>
        <br/>
        <button className={cx(selInvenType == '발' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('발')}>발</button>
        <button className={cx(selInvenType == '목' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('목')}>목</button>
        <button className={cx(selInvenType == '손가락' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('손가락')}>손가락</button>
        <button className={cx(selInvenType == '장신구' ? 'btn-sel' : 'btn-nor')} onClick={() => setInvenType('장신구')}>장신구</button>
        <br/><br/>
        <span>무기의 1차스탯을 선택하세요.</span><br/>
        <button className={cx(selOneStat == '힘' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('힘')}>힘</button>
        <button className={cx(selOneStat == '민첩' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('민첩')}>민첩</button>
        <button className={cx(selOneStat == '지능' ? 'btn-sel' : 'btn-nor')} onClick={() => setSelOneStat('지능')}>지능</button>
      </div>
      <div className={cx('wowitem-view')}>
        {wowitems.length === 0 ? '아이템DB를 내려받는중입니다...' : list}
      </div>
    </div>
  );
};

export default WowItemList;