import React from 'react';
import HeaderWow from 'components/common/HeaderWow';
import FooterWow from 'components/common/FooterWow';
import styles from './PageTemplate2.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

const PageTemplate2 = ({children}) => (
  <div className={cx('page-template2')}>
    <HeaderWow/>
    <div className={cx('main')}>
      {children}
    </div>
    <FooterWow/>
  </div>
);

export default PageTemplate2;