import React from 'react';
import styles from './FooterWow.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

const FooterWow = () => {
  return (
    <footer className={cx('footer-wow')}>
      <div>aliswell86@naver.com</div>
    </footer>
  );
};

export default FooterWow;